<template>
  <div class="text-center animated fadeIn" v-if="upcomingGames">
    <soccer-games :games="upcomingGames.data" :class="!isLoading ? 'animated fadeIn' : null"></soccer-games>
    <loading v-if="isLoading" class="text-center"></loading>
    <no-games v-if="!isLoading && upcomingGames.data && !upcomingGames.data.length"></no-games>
    <div class="row">
      <div class="col">
        <div class="custom-pagination">
          <pagination :data="upcomingGames" :limit="2" @pagination-change-page="loadMoreGames">
            <span class="next_button fa fa-arrow-left" slot="prev-nav"></span>
            <span class="next_button fa fa-arrow-right" slot="next-nav">
            </span>
          </pagination>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

const SoccerGames = () => import("./GamesComponent.vue");
const NoGames = () => import("./../Utilities/NoGames");
const Loading = () => import("./../Utilities/LoadingIndicator");
import Table from './../Utilities/Table'
export default {
  name: "UpcomingSoccer",
  components: {Table, Loading, SoccerGames, NoGames },
  props: ["isGroup"],
  data() {
    return {
      isLoading: true
    };
  },
  mounted() {
    this.getUpcomingGames().then(
      () => {
        this.isLoading = false;
      },
      () => (this.isLoading = false)
    );
  },
  computed: {
    ...mapGetters(["upcomingGames"])
  },
  methods: {
    ...mapActions(["getUpcomingGames"]),
    loadMoreGames (page=1) {
        this.isLoading = true;
        this.$store.dispatch('loadMoreUpcomingGames', page).then(() => {
            this.isLoading = false;
        });
    }
  }
};
</script>

<style>
.custom-pagination {
    display: flex;
   justify-content: center;
}
.page-link {
    position: relative;
    display: block;
    padding: 0.4rem .8rem;
    margin-left: -1px;
    line-height: 1.25;
    color: white;
    background-color: #060f2d !important;
    border: 1px solid #263e77;

}

.page-link > .active {
  background: red;
}
</style>
